import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './style.styl'
/**
 * 
 * 1.  String =>  array
 * lyricObj parseLyric this.formatLyrics[[time,lyric],[time,lyric]]
 * this.formatLyrics state
 *  this.formatLyrics
 * 2. 
 * [[time,lyric,0]]
 * currentTimetime01flag
 * this.formatLyricsstateprops
 * activeLinesstate
 * 3. 
 * toptransition
 */
const parseLyric = (lyricStr) => {
  // 
  let lines = lyricStr.split('\n')
  // [xx:xx.xx]
  const pattern = /\[\d{2}:\d{2}.\d{1,3}\]/g
  // 
  const result = []
  // 
  while (!pattern.test(lines[0])) {
    lines = lines.slice(1)
  }
  // '\n'
  if (!lines[lines.length - 1].length) { lines.pop() }
  for (let i = 0; i < lines.length; i += 1) {
    const line = lines[i]
    const value = line.replace(pattern, '') //   
    //  []:[12...]
    const times = line.match(pattern) || []
    times.forEach((time) => {
      // xx:xx.xx:[xx,xx.xx]
      const t = time.slice(1, -1).split(':')
      // 
      result.push([(parseInt(t[0], 10) * 60) + parseFloat(t[1]), value]) //  [,]
      // result.pushPush
    })
  }
  //   result[0][0]result[1][0]result[0]result[1]
  result.sort((a, b) => a[0] - b[0])
  return result
}


export default class LyricBlock extends Component {
  constructor() {
    super()
    this.state = {
      activeLines: [],
      top: 40,

    }
  }
  componentWillReceiveProps(nextProps) {
    const { currentTime } = nextProps
    if (this.formatLyrics) {
      const len = this.formatLyrics.length
      for (let i = 0; i < len; i += 1) {
        if (currentTime > this.formatLyrics[i][0] - 1) {
          this.setState(
            {
              activeLines: new Array(len)
                .join()
                .split('')
                .map(() => 0),
            },
            () => {
              const copyLines = this.state.activeLines.slice()
              copyLines[i] = 1
              this.setState({
                activeLines: [...copyLines],
                top: 40 + (i * -28),

              })
            },
          )
        }
      }
    }
  }
  renderLyrics = (lyricObj) => {
    if (!lyricObj) return null
    if (lyricObj.nolyric || lyricObj.uncollected) {
      return <p className="lyric-info">o()o</p>
    }
    const lyricStr = lyricObj.lrc.lyric
    this.formatLyrics = parseLyric(lyricStr)
    const { activeLines, top } = this.state
    return (
      <ul
        style={{ top: `${top}px` }}
        className="move-animate lyrics"
      >
        {this.formatLyrics.map((line, index) => (
          <li
            className={activeLines[index] === 1 ? 'line active' : 'line'}
            key={line[0] + Math.random()}
          >
            {line[1]}
          </li>
        ))}
      </ul>
    )
  };
  render() {
    const { lyricObj } = this.props.lyric
    return (
      <div className="lyric-block">
        {lyricObj ? (
          this.renderLyrics(lyricObj)
        ) : (
          <p className="lyric-info">...</p>
        )}
      </div>
    )
  }
}

LyricBlock.protoTypes = {
  lyric: PropTypes.object,
  currentTime: PropTypes.number,
}



